import React,{useState, useEffect} from 'react';
import docs from '../Db/docs'
import {
    Link
    } from "react-router-dom";

const Table = () => {
    const [input, setInput] = useState("")
    const [currentDocs, setCurrentDocs] = useState([])

    useEffect(() => {
        const filterMembers = () => {
            if (input !== ""){
                const res = docs.filter(({name}) => name.includes(input.toLowerCase()))
                setCurrentDocs(res)
            }else{
                setCurrentDocs(docs)
            }
        }
        filterMembers()
    },[input])


    return (
        <div className="commandement_list">
            <div className="presentation_list">
                <h1>LES 10 COMMANDEMENTS</h1>
                <div>
                    <img src="https://images.vexels.com/media/users/3/132068/isolated/preview/f9bb81e576c1a361c61a8c08945b2c48-search-icon-by-vexels.png"/>
                    <input type="text" value={input} onChange={(e) => setInput(e.target.value)} placeholder="Rechercher"/>
                </div>
            </div>
            <div className="table_list">
                <h1>LISTE DES COMMANDEMENTS</h1>
                {
                        currentDocs.map(
                            (doc, index) => {
                                return (
                                    <Link to={"/" + index}>
                                        <div key={index}>
                                                <div><h1>{index}</h1></div>
                                                <div className="cell">
                                                    <h2>{doc.name}</h2>
                                                </div>
                                        </div>
                                    </Link>
                                    
                                )
                            }
                        )
                }
            </div>
        </div>
    )
}

export default Table